import React from "react";
import Navbar from "../components/Navbar";
import styled from "styled-components";
import HomeArticle from "../components/HomeArticle";

let HomeContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

const Home = () => {
  return (
    <HomeContainer>
      <Navbar />
      <HomeArticle />
    </HomeContainer>
  );
};

export default Home;
