import React from "react";
import styled from "styled-components";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";

import Dropdown from "react-bootstrap/Dropdown";
import NavItem from "react-bootstrap/NavItem";
import NavLink from "react-bootstrap/NavLink";
import { Menu } from "@mui/icons-material";

let Container = styled.div`
  display: flex;
  height: 50px;
  justify-content: space-between;
  width: 100%;
`;

let LogoContainer = styled.div`
  display: flex;
  gap: 10px;
  padding-top: 10px;
  padding-left: 10px;
`;

let HomeLogo = styled.h2`
  font-family: "Bruno Ace", cursive;
  font-weight: 500;
`;

let HomeLogoName = styled.h3`
  font-family: "Rubik", sans-serif;
  font-size: 24px;
  font-weight: 400;
`;

const Navbar = () => {
  const handleSelect = (eventKey) => alert(`selected ${eventKey}`);
  return (
    <Container>
      <LogoContainer>
        <HomeLogoName>Jovany</HomeLogoName>
        <HomeLogo>READS</HomeLogo>
      </LogoContainer>
      <Nav>
        <Dropdown as={NavItem}>
          <Dropdown.Toggle as={NavLink} style={{ color: "black" }}>
            {" "}
            <Menu />{" "}
          </Dropdown.Toggle>
          <Dropdown.Menu>
            <Dropdown.Item>All Blogs</Dropdown.Item>
            <NavDropdown.Divider />
            <Dropdown.Item>WHY?</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </Nav>
    </Container>
  );
};

export default Navbar;
